/* CSS Reset */
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    line-height: 1.5;
    font-family: var(--font-playfair-sans), serif;
    margin: 0;
    padding: 0;
    color: #ffffff;
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-clicker-script), cursive;
    font-size: 100%;
    font-weight: normal;
}

ol, ul {
    list-style: none;
}

table {
    border-collapse: collapse;
}
